.button {
    box-shadow:inset 0px 1px 0px 0px #ffffff;
    background:linear-gradient(to bottom, #fdd1d8 5%, #e9e9e9 100%);
    background-color:#fdd1d8;
    border-radius:6px;
    border:1px solid grey;
    display:inline-block;
    cursor:pointer;
    color:#666666;
    font-family:Arial;
    font-size:15px;
    font-weight:bold;
    padding:6px 24px;
    text-decoration:none;
    text-shadow:0px 1px 0px #ffffff;
}
.button:hover {
    background:linear-gradient(to bottom, #e9e9e9 5%, #fdd1d8 100%);
    background-color:#e9e9e9;
}
.button:active {
    position:relative;
    top:1px;
}
.button:focus {
    outline: 0;
}
.button:disabled {
    opacity: .5;
}
